<template>
	<view>
		<view class="u-page padding">
			<view class="" v-for="(unit,key) in audios" :key="key" v-if="unit.length>0">
				<view class="" style="margin-left: 40upx;">
					{{key}}
				</view>
				<view class="contentarea">
					<view class="audio" v-for="(item,index) in unit" :key="index">
						<view class="pannel">
							<view :id="'btn_'+index" :class="curId=='btn_'+index?'pause-bg':'play-bg' " style="width: 24px;height: 24px;background-size: 100% 100%;border-radius: 50%;" :data-index="index" :data-src="item.voice" @click="play"></view>
						</view>
						<view class="info-context">
							<view>{{item.title.length!=0?item.title:' '}}</view>
							<view style="font-size: 12px;">得分:{{item.score}}</view>
						</view>
						<view style="position: absolute; right: 10px; bottom: 10px; font-size: 11px; color: #9e9e9e;">
							时长：{{item.voice_time}}s
						</view>
					</view>
				</view>
			</view>
			<view style="display: flex; justify-content: center; align-items: center; padding: 40upx;">
				<button type="default" @click="loadMore">加载更多</button>
			</view>
		</view>
		<u-tabbar v-model="current" :list="list" active-color="blue" inactive-color="#606266"></u-tabbar>
	</view>
</template>

<script>
	import operateDate from "../../common/operateData.js"
	const audioplayer = uni.createInnerAudioContext();
	export default {
		data() {
			return {
				list:[],
				current:2,
				audios:null,
				curPage:1,
				curId:""
			}
		},
		onLoad(e) {
			this.list = this.$store.state.tabList;
			this.getStudyVoice();
		},
		methods: {
			getStudyVoice(stdId,itemId){
				this.$u.get('/api/get_student_audio',{
					"team_id":2,
					"student_id":1,
					"page":this.curPage,
				}).then(e=>{
					this.audios = operateDate.operateData(e.data,this.audios);
					console.log(this.audios);
				});
			},
			loadMore(){
				this.curPage++;
				this.getStudyVoice(1,2);
			},
			play(e){
				var index = e.target.dataset.index;
				let src = e.target.dataset.src;
				audioplayer.src = src;
				audioplayer.play();
				audioplayer.onPlay(()=>{
					console.log('开始播放');
					this.curId = "btn_" + index;
				});
				audioplayer.onEnded(()=>{
					console.log('结束播放');
					this.curId = "";
				})
			}
		}
	}
</script>

<style>
	.contentarea{
		padding: 15px;
	}
	.audio{
		width: 100%;
		height: 70px;
		margin-top: 10px;
		display: flex;
		flex-direction: row;
		border: 1px solid #e9e9e9;
		position: relative;
	}
	.pannel{
		width: 70px;
		height: 100%;
		display: flex;
		align-items: center;
		justify-content: center;
		background-color: #e9e9e9;
	}
	.info-context{
		padding: 10px;
		display: flex;
		flex-direction: column;
		justify-content: space-between;
	}
	.play-bg{
		background-image: url('data:image/png;base64,');
	}
	.pause-bg{
		background-image: url('data:image/png;base64,');
	}
</style>
